"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Box, Button, Flex, Stack, Heading, Text, Input, Field, Link as ChakraLink, HStack } from "@chakra-ui/react";
import { motion } from "framer-motion";
import { LuArrowLeft, LuMail, LuCheckCircle } from "react-icons/lu";
import { useMutation } from "@tanstack/react-query";
import { toaster } from "../components/ui/toaster";

const MotionBox = motion.create(Box);

export default function ForgotPasswordPage() {
  const router = useRouter();
  const [email, setEmail] = useState("");
  const [submitted, setSubmitted] = useState(false);

  const mutation = useMutation({
    mutationFn: async (email: string) => {
      const res = await fetch("/api/auth/forgot-password", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email }),
      });
      return res.json();
    },
    onSuccess: () => {
      setSubmitted(true);
      toaster.create({ title: "Email Sent", description: "Check your inbox for instructions.", type: "success" });
    },
  });

  return (
    <Flex minH="100vh" align="center" justify="center" bg="gray.50" _dark={{ bg: "gray.950" }} p={4}>
      <MotionBox initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} w="full" maxW="md">
        <Stack gap={8} p={10} bg="white" _dark={{ bg: "gray.900" }} borderRadius="3xl" shadow="2xl">
          {!submitted ? (
            <>
              <Stack textAlign="center">
                <Heading size="2xl" fontWeight="black">Reset Password</Heading>
                <Text color="gray.500">Enter your email and we'll send you a link to get back into your account.</Text>
              </Stack>

              <form onSubmit={(e) => { e.preventDefault(); mutation.mutate(email); }}>
                <Stack gap={6}>
                  <Field.Root>
                    <Field.Label fontWeight="bold" fontSize="xs">EMAIL ADDRESS</Field.Label>
                    <Input 
                      size="lg" borderRadius="xl" placeholder="name@company.com" 
                      value={email} onChange={(e) => setEmail(e.target.value)} required 
                    />
                  </Field.Root>
                  <Button type="submit" colorPalette="teal" size="xl" borderRadius="xl" loading={mutation.isPending}>
                    Send Reset Link
                  </Button>
                </Stack>
              </form>
            </>
          ) : (
            <Stack textAlign="center" gap={6}>
              <Box color="teal.500" mx="auto">
                <LuCheckCircle size={60} />
              </Box>
              <Heading size="xl">Check your email</Heading>
              <Text color="gray.500">We've sent a password reset link to <b>{email}</b></Text>
              <Button variant="outline" size="lg" borderRadius="xl" onClick={() => setSubmitted(false)}>
                Try another email
              </Button>
            </Stack>
          )}

          <ChakraLink 
            display="flex" alignItems="center" justifyContent="center" gap={2} 
            fontSize="sm" fontWeight="bold" color="gray.500" onClick={() => router.push("/login")}
          >
            <LuArrowLeft /> Back to Login
          </ChakraLink>
        </Stack>
      </MotionBox>
    </Flex>
  );
}